// {"title":"AngleSharp v0.11.0 released","date":"2019-02-12 09:20:00","categories":"news release major","author":"Florian Rappl","userId":"FlorianRappl","section":"Posts","link":"/blog/2019-02-12-anglesharp-v011-released","source":"/home/runner/work/anglesharp.github.io/anglesharp.github.io/docs/blog/2019-02-12-anglesharp-v011-released.md"}
import * as React from 'react';
import { ContentPage } from '@pidoc/components';

    import { PageContent, Markdown, PageLayout } from '@pidoc/components';

    const link = "https://github.com/AngleSharp/anglesharp.github.io/tree/main/docs/blog/2019-02-12-anglesharp-v011-released.md";
    const html = `
<p>Yesterday the latest version of AngleSharp has been released. This version corrects some smaller issues of the big 0.10 release published a month ago. It also brings some further advancements such as improved fragment parsing, some internal bugfixes, or the removal of AngleSharp.Xml from the core library.</p>
<p>The removal of AngleSharp.Xml has been done for several reasons. For once, not many people have been interested in or actively using this part of AngleSharp. The other reason is that this way AngleSharp.Xml can have its own release cycle and expand beyond the constraint feature set determined by AngleSharp.Core. This logic closely follows the logic for extracting AngleSharp.Css out of the core.</p>
<p>As a first consequence the <code>AutoSelectedMarkupFormatter</code> has been removed from AngleSharp.Core. It now lives in AngleSharp.Xml as the <code>XhtmlMarkupFormatter</code> is also part of AngleSharp.Xml.</p>
<p>Finally, this release also expands the documentation with more information regarding the migration steps and examples to get started.</p>
`;
    const meta = {"title":"AngleSharp v0.11.0 released","date":"2019-02-12 09:20:00","categories":"news release major","author":"Florian Rappl","userId":"FlorianRappl","section":"Posts"};
  

export default () => (
  <ContentPage>
    
    <PageLayout name={"blog"} meta={meta}>
      <PageContent meta={meta}>
        <Markdown content={html} link={link} editLabel={"Edit on GitHub"} />
      </PageContent>
    </PageLayout>
  
  </ContentPage>
);